﻿import { Injectable, EventEmitter } from '@angular/core';

export enum ENotificationType {
	Information,
	Warning,
	Success,
	Error
}

export interface INotificationMessageOptions {
	message: string,
	type: ENotificationType,
	canClose?: boolean
}

@Injectable({
	providedIn: 'root'
})

export class UserNotificationService {
	messages: INotificationMessageOptions[] = [];
	public updated = new EventEmitter<void>();

	public error(message: string) {
		this.show({ message, type: ENotificationType.Error });
	}

	public warning(message: string) {
		this.show({ message, type: ENotificationType.Warning });
	}

	public information(message: string) {
		this.show({ message, type: ENotificationType.Information });
	}

	public success(message: string) {
		this.show({ message, type: ENotificationType.Success });
	}

	public show(message: INotificationMessageOptions) {
		if (message.canClose == undefined) {
			message.canClose = true;
		}

		this.messages.push(message);
		this.updated.emit();
	}

	public remove(message: INotificationMessageOptions) {
		let i = this.messages.indexOf(message);
		if (i >= 0) {
			this.messages.splice(i, 1);
			this.updated.emit();
		}
	}
}