﻿import { Component, OnInit } from '@angular/core';

import { UserNotificationService, ENotificationType, INotificationMessageOptions } from '../../services/user-notification.service';

@Component({
	selector: 'app-notification-area',
	templateUrl: './notification-area.component.html',
	styleUrls: ['./notification-area.component.less']
})

export class NotificationAreaComponent implements OnInit {
	ENotificationType = ENotificationType

	constructor(
		public userNotificationService: UserNotificationService,
	) {
	}

	ngOnInit() {
	}

	removeMessage(message: INotificationMessageOptions) {
		this.userNotificationService.remove(message);
	}
}